import { h, Component } from 'preact';
import PropTypes from 'prop-types';
import { bind, memoize, debounce } from 'decko';
import {fieldNameToLabel} from '@/utils'

export default class Base extends Component {
	constructor() {
		super()

		this.state.label = this.getControlLabel()
	}

	componentDidMount() {
		this.setState({ label: this.getControlLabel() });
	}

	linkValueToOrigin(getter, setter) {
		let oo = this.props.origin,
			name = this.props.name,
			self = this
		if (oo) {
			delete oo[name]
			Object.defineProperty(oo, name, {
				//getter, setter
				get : getter || (() => {
					return self.getFormatedValue()
				}),
				set: setter || (newValue => {
					self.onChange(newValue)
				})
			});
		}
	}

	//@bind
	getControlLabel() {
        return this.props ? this.props.label || fieldNameToLabel(this.props.name) : '_Unnamed_';
    }

    getFormatedValue() {
		return this.state.value
	}

    onChange(newValue, overrideCallback) {
		if (this.__onChangeGuard)
			return console.log(`onChange already invoked for ${this.props.name}. Halting call to prevent infinite loop.`)

		this.__onChangeGuard = true

        const cb = overrideCallback || this.props.onChange
        if (cb)
            cb(newValue)

		if (this.props.notifyParentOnChange)
            this.props.notifyParentOnChange(this.state.value, this.props.name)

		this.__onChangeGuard = false
    }
}
export { Base }

Base.propTypes = {
	// required
	name: PropTypes.string.isRequired,
	value: PropTypes.oneOfType([
		PropTypes.string,
		PropTypes.number,
	]).isRequired,
	// optional
	origin: PropTypes.object,
	onChange: PropTypes.func,
    notifyParentOnChange: PropTypes.func,
}



